}
@media screen and (min-width: 376px) {
  .br-ss {
    display: none;
  }
}

.br-sm {
  display: block;
}
@media screen and (min-width: 600px) {
  .br-sm {
    display: none;
  }
}

.br-481 {
  display: block;
}
@media screen and (max-width: 481px) {
  .br-481 {
    display: none;
  }
}

.br-ss {
  display: none;
}
@media screen and (max-width: 375px) {
  .br-ss {
